*{
margin:0;
padding:0;
}
body {
    background-color:whitesmoke;
}
th {
    min-width:75px;
    min-height:12px;
    border:1px solid black;
    text-align:center;
    color:red;
    font-size:10px;
    font-weight: 500;
}
tr {
    min-width:75px;
    min-height:12px;
    display:block;
    text-align:center;
    border:1px solid black;
    color:green;
    font-weight: 600;
}
form {
    margin-top:7%;
    margin-bottom:2%;
    width:100%;
    text-align:center;
}
table {
    margin:0 auto;
    text-align:center;
}
a {
    text-decoration:none;
    color:red;
}
input {
    margin-left:2%;
}
button {
    margin-left:1%;
    font-weight: 700;
    border-radius: 5px;
    cursor:pointer;
    box-shadow: none;
    background-color:whitesmoke;
}
